@import "../../../../GlobalCss/GlobalCss.scss";

.DonationHeaderIntroSectionMainContainer {
    font-family: $ComfertaFont;
    margin-top: 65px;

    .DonationHeaderIntroSectionContainer {
        position: relative;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        min-height: 78vh;
        color: #fff;
        overflow: hidden;

        .DonationHeaderImageintro {
            img {
                position: absolute;
                top: 0%;
                left: 0%;
                z-index: -1;
                // opacity: 0.9;
            }
        }

        h1 {
            text-align: center;
            font-weight: 900;
            font-size: 64px;
            // color: #90BA93;
            // color: #246359;
            letter-spacing: 2px;

            text-shadow: 0px 2px, 2px 0px, 2px 2px;
            color: #FCD24C;
        }

        p {
            text-align: center;
            width: 70%;
            font-size: 24px;
            font-weight: 700;
        }

        .DonationHeaderIntroSectionContainerBtn {
            margin: 2em 0 0 0;
        }
    }
}

@media (max-width:900px) {
    .DonationHeaderIntroSectionMainContainer {
        font-family: $ComfertaFont;
        margin-top: 65px;
        max-height: 68vh !important;
        
        .DonationHeaderIntroSectionContainer {
            color: #fff;
            overflow: hidden;

            .DonationHeaderImageintro {
                img {
                    left: -100%;
                }
            }

            h1 {
                font-size: 48px;
            }

            p {
                text-align: center;
                width: 100%;
                font-size: 14px;
                font-weight: 700;
            }

            .DonationHeaderIntroSectionContainerBtn {
                margin: 2em 0 0 0;
            }
        }
    }

}